@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navBarContainer{background-color:#FFE600;height:64px;display:flex;justify-content:center;align-items:center;position:relative}.navBarContainer__logo{width:150px;position:absolute;left:40px}.navBarContainer__compraAhora{width:350px;position:absolute;right:30px}.navBarContainer__searchContainer{position:relative;width:45%}.navBarContainer__input{height:35px;width:100%;border:none;border-radius:5px;outline:none;padding-left:10px;font-size:20px;font-weight:400}.navBarContainer__searchButton{background-color:transparent;border:none;position:absolute;right:0;top:1px;border-left:1px solid #dedddd;height:90%;cursor:pointer}@media (max-width: 720px){.navBarContainer__logo{display:none}.navBarContainer__searchContainer{width:80%}}

.productItem{background-color:white;padding:10px;border-radius:5px;cursor:pointer;transition:height .3s;width:250px;height:340px;font-family:'Nunito', sans-serif}.productItem:hover{height:380px}.productItem:hover>.productItem__moreInfo{height:20px;opacity:1}.productItem__separation{position:relative}.productItem__line{border:1px solid #dedddd;width:94%}.productItem__container-carrito{border:1px solid #dedddd;border-radius:50%;padding:5px;display:flex;justify-content:center;width:20px;align-items:center;position:absolute;background-color:#fff;top:-13px;right:15px}.productItem__carrito{width:20px;fill:#39b54a}.productItem__img{width:200px;height:250px;display:flex;margin:0 auto}.productItem__price{text-align:left;color:#333333;font-size:23px;font-weight:600;margin:5px auto;width:90%}.productItem__cuota{margin:5px auto;color:#333333;width:90%;font-size:14px;font-weight:600}.productItem__moreInfo{height:0px;transition:opacity .2s .2s, height .2s .2s;opacity:0}.productItem_info{width:94%;margin:0 auto;font-weight:500;color:#505050;font-size:14px}

.homeContainer{background-color:#EBEBEB}.homeContainer__products{margin:10px 50px;display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-auto-rows:400px}

.pais__item{background-color:#ffffff82;margin:1px 0px;padding:1px 11px;border-radius:5px}.pais__item:hover{background-color:#ffffffad}

.containerPaises{background-color:#FFE600;min-height:100vh;display:flex;justify-content:center;align-items:center}.containerPaises__logo{width:400px}.containerPaises__listaPaises{display:flex;flex-direction:column}

